<template>
    <el-row>
        <basic-header :items="['管理员管理','查看管理员']"></basic-header>
        <el-tabs v-model="activeName">
            <el-tab-pane label="查看管理员" name="show">
                <el-form :inline="true">
                    <el-form-item>
                        <el-input placeholder="请输入管理员姓名" v-model="value"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button circle type="primary" icon="el-icon-search" @click="search"></el-button>
                    </el-form-item>
                </el-form>
                <el-table :data="tableData">
                    <el-table-column label="id" prop="id"></el-table-column>
                    <el-table-column label="用户名" prop="username"></el-table-column>
                    <el-table-column label="状态" prop="state">
                        <template #default="o">
                            <div v-if="o.row.state===1">白名单</div>
                            <div v-else>黑名单</div>
                        </template>
                    </el-table-column>
                    <el-table-column label="上次登录时间" prop="last_login_time"></el-table-column>
                    <el-table-column label="操作">
                        <template #default="o">
                            <el-button type="text" @click="del(o.row.id)">删除</el-button>
                            <el-button type="text" v-if="o.row.state===1" @click="move(o.row.id,2)">加入黑名单</el-button>
                            <el-button type="text" v-else @click="move(o.row.id,1)">移出黑名单</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <el-pagination
                    background
                    layout="prev,pager,next"
                    :current-page.sync="page"
                    :page-size.sync="pageSize"
                    :total="total"
                    @current-change="handleChange"
                    style="margin-top: 30px"></el-pagination>
            </el-tab-pane>
            <el-tab-pane label="添加管理员" name="add">
                <el-col :span="12">
                <el-form label-width="80px" :model="form" :rules="rules" ref="addForm">
                    <el-form-item label="姓名" prop="username">
                        <el-input v-model="form.username"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="add">新增</el-button>
                    </el-form-item>
                </el-form>
                </el-col>
            </el-tab-pane>
        </el-tabs>

    </el-row>
</template>

<script>
    import BasicHeader from "@/components/BasicHeader"
    export default {
        name: "ManagerShow",
        data: () => ({
            activeName:"add",
            tableData:[],
            //翻页的三个属性
            total:0,//总条数
            page:1,//当前页码
            pageSize:5, //每一页可以显示的数量
            value:"",//搜索的内容
            form:{
                username:""
            },
            rules:{
                username:{required:true,message:"请输入用户名",trigger:"blur"}
            }

        }),
        methods: {
            fetchData(){
                this.$axios.get(this.$api.MANAGER,{role:2,page:this.page,pageSize:this.pageSize,search:this.value}).then(res=>{
                   if(res.data.code===200){
                       this.tableData=res.data.data;
                       this.total=res.data.total;
                   }
                });
            },
            handleChange(){
                this.fetchData();
            },
            search(){
                this.page=1;
                this.fetchData();
            },
            del(id){
                this.$confirm('此操作将永久删除该管理员, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$axios.delete(this.$api.MANAGER,{id}).then(res=>{
                        if(res.data.code===200){
                            this.$message.success("删除成功");
                            this.fetchData();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            move(id,state){
                this.$axios.put(this.$api.MANAGER,{id,state}).then(res=>{
                    if(res.data.code===200){
                        this.$message.success("修改成功");
                        this.fetchData();
                    }
                })
            },
            add(){
                this.$refs.addForm.validate(valid=>{
                    if(valid){
                        this.$axios.post(this.$api.MANAGER,this.form).then(res=>{
                            if(res.data.code===200){
                                this.$message.success("新增成功");
                                this.activeName="show";
                                this.fetchData();
                                this.$refs.addForm.resetFields();
                            }
                        })
                    }else{
                        return false;
                    }
                })
            }
        },
        components:{
            "basic-header":BasicHeader
        },
        mounted() {
            this.fetchData();
        }
    }
</script>

<style scoped lang="scss">

</style>